import React, { Component } from 'react';
import { Alert } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xalert extends Component {

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	render() {
		const { defaultOptions} = this.props.configs

		return (
			<div id="x-alert">
				<h3>Alert 警告</h3>
				<div className="mt-20 mb-20">
					<Alert title="成功提示的文案" type="success" />
					<Alert title="消息提示的文案" type="info" />
					<Alert title="警告提示的文案" type="warning" />
					<Alert title="错误提示的文案" type="error" />
				</div>
				<Codemirror className="mb-20" value={
					`<Alert title="成功提示的文案" type="success" />
<Alert title="消息提示的文案" type="info" />
<Alert title="警告提示的文案" type="warning" />
<Alert title="错误提示的文案" type="error" />`
				} options={defaultOptions} />
			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(Xalert)
